.color-txt {

    &:hover {
        background-clip: none; 
        -webkit-background-clip: text;
        color: transparent;
        transform: translateY(0.2rem);
        transition: .2s;
        
        &:nth-child(2) {
            background-image: linear-gradient( 107deg,  rgba(2,108,223,1) 27.4%, rgba(0,255,255,1) 92.7% );
        }
        &:nth-child(3) {
            background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,37,174,1) 0%, rgba(241,147,55,1) 53.4%, rgba(250,237,56,1) 99.5% );
        }
        &:nth-child(4) {
            background-image: linear-gradient( 109.6deg,  rgba(31,179,237,1) 11.2%, rgba(17,106,197,1) 91.1% );
        }
        &:nth-child(5) {
            background-image: linear-gradient( 117.8deg,  rgba(240,19,77,1) 22.2%, rgba(228,0,124,1) 88.7% );
            // background-image: linear-gradient( 109.6deg,  rgba(98,210,141,0.88) 11.2%, rgba(234,245,45,0.79) 88% );
        }
    }

}

