.footer {
    padding: 0rem 10rem;
    font-family: 'suisse';

    @media(max-width: 768px) {
        padding: 0rem 4rem;
    }

    &__2020 {
        background-image: linear-gradient( 122.3deg,  rgba(111,71,133,1) 14.6%, rgba(232,129,166,1) 29.6%, rgba(237,237,183,1) 42.1%, rgba(244,166,215,1) 56.7%, rgba(154,219,232,1) 68.7%, rgba(238,226,159,1) 84.8% );
        font-size: 33vw;
        text-align: center;
        background-clip: none;
        color: transparent;
        padding: 0rem; 
        -webkit-background-clip: text;

        @media(max-width: 768px) {
            font-size: 34vw;
        }
    }
    
    &__span {
        font-family: 'voyage';
        font-size: 3rem;
        position: relative;
        top: 3rem;
        left: 3rem;
    }

    &__credit {
        color: var(--white);
        display: flex;
        justify-content: space-between;
        font-family: 'Barlow Condensed';
        font-weight: 200;
        font-size: 1.8rem;
        position: relative;
        top: -5rem;

        @media(max-width: 768px) {
            top: -2rem;
            font-size: 1.5rem;
        }
    }

    &__privacy {
        color: var(--white-light);
        display: flex;
        justify-content: space-between;
        font-size: 1.6rem;
        padding-bottom: 3rem;

        a {
            color: var(--white-light);
        }
    }
}