.project {
    background-color: var(--black);
    padding: 10rem;
    display: flex;
    flex-direction: column; 
    transition: transform 1s;

    @media(max-width: 768px) {
        padding: 6rem;
    }

    &__item {
        position: relative;
        display: block;
        max-width: 55%; 
        margin: 6rem 3rem;
        background-color: black;
        transition: opacity 4s; 
    
        @media(max-width: 768px) {
            margin: 6rem 0rem;
        }
    
        @media(max-width: 600px) {
            max-width: 100%;
        }
    
        &:nth-child(even) {
            min-width: 55%;   
            align-self: flex-end; 
            
            @media(max-width: 600px) {
                min-width: 100%;
            }
        }
    
        img {
            width: 100%;
            overflow: hidden;
            transform: scale(1);
            transition: .3s;  
            
            &:hover {
                transform: scale(1.1);
                opacity: .7;
            }
        }
    }

    &__title {
        position: absolute;
        top: 50%;
        left: 100%;
        transform: translate(5%, -50%);
        font-family: 'voyage';
        mix-blend-mode: difference;
        font-size: 12rem;

        @media(max-width: 768px) {
            font-size: 8rem;
        }

        @media(max-width: 600px) {
            position: initial;
            transform: translate(0%, 0%);
            font-size: 3.5rem;
            text-align: center;
            font-weight: 400;
            letter-spacing: .1rem;
        }

        &--1 {
            left: -60%;
        }
    }
}

h3 {
    text-align: center;
    font-size: 5.5rem;
    letter-spacing: .4rem;
    color: black; 
    
    font-family: 'Formula Condensed';
}

